import React, { useEffect, useState } from "react";
import "./LearnMorePage.css";
import { Link } from "react-router-dom";

const LearnMorePage = () => {
  const [data, setData] = useState([]);

  useEffect(() => {
    fetch("http://localhost:5500/api/learnmore")
      .then(res => res.json())
      .then(data => setData(data))
      .catch(err => console.error(err));
  }, []);

  return (
    <div className="learnmore-wrap">
      {data.map((item, index) => (
        <React.Fragment key={index}>
          <div className="learnmore-content">
            <h1 className="learnmore-title">{item.title}</h1>
            {item.paragraphs.map((p, idx) => (
              <p className="learnmore-text" key={idx}>{p}</p>
            ))}
            {item.buttonText && item.buttonPath && (
              <Link to={item.buttonPath} className="learnmore-btn">
                {item.buttonText}
              </Link>
            )}
          </div>

          <div className="learnmore-image-wrap">
            <img
              src={item.image}
              alt={item.title}
              className="learnmore-image"
            />
          </div>
        </React.Fragment>
      ))}
    </div>
  );
};

export default LearnMorePage;
